import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap";
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb";
import FiscalYearPeriodsTable from './Tables/FiscalYearPeriodsTable';
import FiscalYearPeriodsModal from './Modals/FiscalYearPeriodsModal';
import FiscalYearSearch from '../../SearchComponents/FiscalYearSearch/FiscalYearSearch';
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";

import { toastr } from 'react-redux-toastr';

class FiscalYearPeriods extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            columns: [],
            data: [],
            filteredData: [],
            value: '',
            show: false,
            showdelete: false,
            ID: 0,
            PERIOD: 1,
            FISCALYEARID: 0,

            FISCALYEARNAME: '',
            PYEAR: 0,
            PERIODOPEN: 0,
            PERIODCLOSED: 0,
            CREATEDBYID: 1,
            LASTMODIFIEDBYID: 1,
            DATECREATED: Date.ShowDate(),
            DATELASTMODIFIED: Date.ShowDate(),
            InsertUpdate: '',

            opensearch: false,

            searchcolumns: [],
            searchdata: [],
            searchFilteredData: [],
            searchvalue: '',

        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowSearch = () => {
            this.setState({ opensearch: true });
        };

        this.handleHideSearch = () => {
            this.setState({ opensearch: false });
        };

        this.populateData();
        this.populateTypes();

    }

    populateData() {
        const getdata = `
        query{
            FiscalYearPeriods{
            ID
            PERIOD
            PYEAR
            PERIODOPEN
            PERIODCLOSED
            FISCALYEARID
            FISCALYEAR{
            ID
            FISCALYEARNAME
            }
            
            CREATEDBYID
            LASTMODIFIEDBYID
            DATECREATED
            DATELASTMODIFIED
            }
           }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.FiscalYearPeriods
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    populateTypes() {
        const getdata = `
            query {
                FiscalYears{
                    ID
                    FISCALYEARNAME
                    }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchdata: data.data.FiscalYears
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to RMS Module Load Data');
                }
            })
    }

    handleSubmit = (event) => {
        event.preventDefault();

        const form = `
        mutation{
            addFiscalYearPeriods(
                PERIOD:${this.state.PERIOD},
                FISCALYEARID:${this.state.FISCALYEARID},
                PYEAR:${this.state.PYEAR},
                PERIODOPEN:${this.state.PERIODOPEN},
                PERIODCLOSED:${this.state.PERIODCLOSED},

                CREATEDBYID:${this.state.CREATEDBYID},
                LASTMODIFIEDBYID:${this.state.LASTMODIFIEDBYID},
                DATECREATED:"${this.state.DATECREATED}",
                DATELASTMODIFIED:"${this.state.DATELASTMODIFIED}"
                ){
                ID
            }
        }
        `;

        const formEdit = `
        mutation{
            updateFiscalYearPeriods(
                ID:${this.state.ID},
                PERIOD:${this.state.PERIOD},
                FISCALYEARID:${this.state.FISCALYEARID},
                PYEAR:${this.state.PYEAR},
                PERIODOPEN:${this.state.PERIODOPEN},
                PERIODCLOSED:${this.state.PERIODCLOSED},
                LASTMODIFIEDBYID:${this.state.LASTMODIFIEDBYID},
                DATELASTMODIFIED:"${this.state.DATELASTMODIFIED}"
                ){
                ID
            }
        }
        `;

        if (this.state.InsertUpdate === 'Insert') {
            API.create(form)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.success('Create Record', 'Record Created Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        } else {
            API.update(formEdit)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.info('Update Record', 'Record Updated Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        }

    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
        mutation{
            deleteFiscalYearPeriods(
                ID:${this.state.ID}
                ){
                ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseSearch = (e) => {
        this.setState({
            FISCALYEARID: e.ID,
            FISCALYEARNAME: e.FISCALYEARNAME,
            opensearch: false,
        });
    };

    handleEditData = (e) => {
        this.setState({
            ID: e.ID,
            PERIOD: e.PERIOD,
            FISCALYEARID: e.FISCALYEARID,
            FISCALYEARNAME: e.FISCALYEAR.FISCALYEARNAME,
            PYEAR: e.PYEAR,
            PERIODOPEN: e.PERIODOPEN,
            PERIODCLOSED: e.PERIODCLOSED,

            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            ID: e.ID,
            PERIOD: e.PERIOD,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            PERIOD: '',
            FISCALYEARID: '',
            PYEAR: '',
            PERIODOPEN: '',
            PERIODCLOSED: '',
            FISCALYEARNAME: ''
        })
    }

    handleFilter = (e) => {

        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.PERIOD.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.PERIOD.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchFilter = (e) => {

        let value = e.target.value
        let data = this.state.searchdata
        let filteredData = this.state.searchFilteredData
        this.setState({ searchvalue: value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.FISCALYEARNAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.FISCALYEARNAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    render() {
        debugger;
        const { data, value, filteredData, searchdata, searchvalue } = this.state
        return (
            <>
                <Helmet><title>Fiscal Year Periods</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Fiscal Year Periods"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Fiscal Year Periods</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <FiscalYearSearch
                                        handleSearchFiscalYear={this.handleCloseSearch}
                                        opensearch={this.state.opensearch}
                                        handleHideSearch={this.handleHideSearch}
                                        value={searchvalue}
                                        data={searchdata}
                                        handleFilter={this.handleSearchFilter}
                                        filteredData={filteredData}
                                    />

                                    <FiscalYearPeriodsTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <FiscalYearPeriodsModal
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowSearch={this.handleShowSearch}
                                        FiscalYearID={this.state.FISCALYEARID}
                                        handleChangeFiscalYearID={e => this.setState({ FISCALYEARID: e.target.value })}

                                        FiscalYearName={this.state.FISCALYEARNAME}
                                        handleChangeFiscalYearName={e => this.setState({ FISCALYEARNAME: e.target.value })}

                                        Period={this.state.PERIOD}
                                        changePeriod={e => this.setState({ PERIOD: e.target.value })}

                                        PYear={this.state.PYEAR}
                                        changePYear={e => this.setState({ PYEAR: e.target.value })}

                                        PeriodOpen={this.state.PERIODOPEN}
                                        changePeriodOpen={e => this.setState({ PERIODOPEN: e.target.value })}

                                        PeriodClosed={this.state.PERIODCLOSED}
                                        changePeriodClosed={e => this.setState({ PERIODCLOSED: e.target.value })}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(FiscalYearPeriods));